import axios from 'axios';
import {useLocation} from 'react-router-dom';
import {useNavigate} from 'react-router-dom';
import React, {useEffect, useState} from 'react';
import useEmblaCarousel from 'embla-carousel-react';

import {text} from '../text';
import {utils} from '../utils';
import {items} from '../items';
import {URLS} from '../config';
import {hooks} from '../hooks';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {product} from '../product';
import {actions} from '../store/actions';
import {components} from '../components';

export const Product: React.FC = () => {
  const location = useLocation();
  const item = location.state.item;

  const navigate = useNavigate();
  const dispatch = hooks.useAppDispatch();

  const [emblaRef, emblaApi] = useEmblaCarousel();

  const handleSlideChange = () => {
    if (emblaApi) {
      setActiveIndex(emblaApi.selectedScrollSnap());
    }
  };

  useEffect(() => {
    if (emblaApi) {
      emblaApi.on('select', handleSlideChange);
    }
  }, [emblaApi]);

  const [reviewsData, setReviewsData] = useState<any>([]);
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const [selectedColor, setSelectedColor] = useState<string>(
    item.colors?.[1].name
  );

  const modifiedItem = {
    ...item,
    color: selectedColor,
  };

  const [activeIndex, setActiveIndex] = useState(0);

  const getReviews = async () => {
    setIsLoading(true);
    try {
      const reviews = await axios
        .get(URLS.GET_REVIEWS)
        .then((res) => res.data.reviews);

      setReviewsData(reviews);
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    getReviews();
  }, []);

  const renderHeader = (): JSX.Element => {
    return <components.Header goBack={true} line={true} basket={true} />;
  };

  const renderIndicator = (): JSX.Element => {
    return (
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          position: 'absolute',
          bottom: 30,
          alignSelf: 'center',
          flexDirection: 'row',
          width: '100%',
          gap: 6,
        }}
      >
        {item.images?.map((item: any, index: number) => {
          const isSelected = index === activeIndex;
          return (
            <div
              style={{
                background: isSelected
                  ? theme.colors.mainColor
                  : theme.colors.white,
                width: 10,
                height: 10,
                borderRadius: 5,
                borderWidth: 1,
                borderStyle: 'solid',
                borderColor: isSelected ? theme.colors.mainColor : '#E7EBEB',
              }}
              key={index}
            />
          );
        })}
      </div>
    );
  };

  const renderCarouselContent = (): JSX.Element => {
    return (
      <div className='embla' ref={emblaRef}>
        <div className='embla__container'>
          {item.images?.map((item: any, index: any, array: any) => {
            return (
              <div
                style={{
                  height: 500,
                  width: '100%',
                  cursor: 'pointer',
                  backgroundColor: theme.colors.imageBackground,
                }}
                key={index}
                className='embla__slide'
              >
                <img
                  src={item}
                  alt='Carousel'
                  style={{
                    width: '100%',
                    height: '100%',
                    objectFit: 'contain',
                  }}
                />
              </div>
            );
          })}
        </div>
      </div>
    );
  };

  const renderCarousel = (): JSX.Element => {
    return (
      <section style={{position: 'relative', marginBottom: 30}}>
        {renderCarouselContent()}
        {renderIndicator()}
      </section>
    );
  };

  const renderNameWithButton = (): JSX.Element => {
    return (
      <div
        style={{
          marginBottom: 30,
          padding: '0 20px 0 20px',
          ...utils.rowCenterSpcBtween(),
        }}
      >
        <text.H3 style={{color: theme.colors.mainColor}}>{item.name}</text.H3>
        <div style={{...utils.rowCenter()}}>
          <svg.StarSvg />
          <text.T12 style={{marginLeft: 4, color: theme.colors.textColor}}>
            {item.rating.toFixed(1)}
          </text.T12>
        </div>
      </div>
    );
  };

  const renderPriceWithQuantity = (): JSX.Element => {
    return (
      <div
        style={{
          padding: '10px 4px 10px 20px',
          margin: '0 0 30px 20px',
          borderTopLeftRadius: 10,
          borderBottomLeftRadius: 10,
          ...utils.rowCenterSpcBtween(),
          border: `1px solid ${theme.colors.antiFlashWhite}`,
          borderRight: '0px',
        }}
      >
        <div
          style={{
            fontSize: 20,
            ...theme.fonts.DM_Sans_700Bold,
            color: theme.colors.mainColor,
          }}
        >
          ${item.price}
        </div>
        <product.ProductCounterInner item={modifiedItem} />
      </div>
    );
  };

  const renderColors = (): JSX.Element => {
    return (
      <div style={{marginBottom: 40, padding: '0 20px 0 20px'}}>
        <text.H5 style={{marginBottom: 20, color: theme.colors.mainColor}}>
          Color
        </text.H5>
        <div
          style={{
            gap: 10,
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            flexWrap: 'wrap',
          }}
        >
          {item.colors?.map((item: any, index: number) => {
            return (
              <button
                key={index}
                style={{
                  width: 30,
                  height: 30,
                  borderRadius: 15,
                  flexDirection: 'row',
                  alignItems: 'center',
                  backgroundColor: item.code,
                  justifyContent: 'center',
                  flexWrap: 'wrap',
                  border:
                    item.name === 'White'
                      ? '#E7EBEB'
                      : theme.colors.transparent,
                  borderStyle: 'solid',
                  borderWidth: 1,
                }}
                onClick={() => {
                  setSelectedColor(item.name);
                }}
              >
                {selectedColor === item.name && (
                  <svg.CheckSvg
                    color={
                      item.name === 'White'
                        ? theme.colors.mainColor
                        : theme.colors.white
                    }
                  />
                )}
              </button>
            );
          })}
        </div>
      </div>
    );
  };

  const renderDescription = (): JSX.Element => {
    return (
      <div style={{marginBottom: 40, padding: '0 20px 0 20px'}}>
        <text.H5 style={{marginBottom: 20, color: theme.colors.mainColor}}>
          Description
        </text.H5>
        <text.T16 style={{marginBottom: 20, color: theme.colors.textColor}}>
          {item.description}
        </text.T16>
        <button
          onClick={() => {
            navigate('/description', {state: {item}});
          }}
        >
          <svg.ReadMoreSvg />
        </button>
      </div>
    );
  };

  const renderReviews = (): JSX.Element => {
    const reviewQty = 2;
    return (
      <div style={{marginBottom: 20}}>
        <components.BlockHeading
          title={`Reviews (${reviewsData.slice(0, reviewQty).length})`}
          containerStyle={{padding: '0 20px 0 20px', marginBottom: 20}}
          viewAllOnClick={() =>
            navigate('/reviews', {state: {reviews: reviewsData}})
          }
        />
        {reviewsData
          .slice(0, reviewQty)
          .map((item: any, index: number, array: any) => {
            const isLast = index === array.length - 1;
            return (
              <items.ReviewItem
                item={item}
                isLast={isLast}
                key={item.id.toString()}
              />
            );
          })}
      </div>
    );
  };

  const renderButtons = (): JSX.Element => {
    return (
      <>
        <components.Button
          title='Add to cart'
          onClick={() => {
            dispatch(actions.addToCart(modifiedItem));
          }}
          containerStyle={{marginBottom: 10, padding: '0 20px 0 20px'}}
        />
      </>
    );
  };

  const renderContent = (): JSX.Element => {
    if (isLoading) return <components.Loader />;

    return (
      <main style={{padding: '0 0 20px 0', marginTop: 52}}>
        {renderCarousel()}
        {renderNameWithButton()}
        {renderPriceWithQuantity()}
        {renderColors()}
        {renderDescription()}
        {renderReviews()}
        {renderButtons()}
      </main>
    );
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
    </>
  );
};
