import {FC} from 'react';

import {text} from '../text';
import {hooks} from '../hooks';
import {utils} from '../utils';
import {custom} from '../custom';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {components} from '../components';

const renderHeader = (): JSX.Element => {
  return <components.Header goBack={true} />;
};

export const SignUp: FC = (): JSX.Element => {
  const navigate = hooks.useAppNavigate();

  const renderContent = (): JSX.Element => {
    const socialStyle: React.CSSProperties = {
      width: '30%',
      height: 40,
      ...utils.flexCenter(),
      borderRadius: '10px',
      border: '1px solid #E8EFF4',
    };

    return (
      <main style={{padding: '50px 20px 20px 20px', marginTop: 52}}>
        <text.H1 style={{marginBottom: 40}}>Sign up</text.H1>
        <custom.InputField
          label='name'
          placeholder='Olivia Bennett'
          icon={<svg.InputCheckSvg />}
          containerStyle={{marginBottom: 20}}
        />
        <custom.InputField
          label='Email'
          icon={<svg.InputCheckSvg />}
          placeholder='oliviabennett@mail.com'
          containerStyle={{marginBottom: 20}}
        />
        <custom.InputField
          clickable={true}
          label='password'
          placeholder='••••••••'
          type='password'
          icon={<svg.EyeOffSvg />}
          containerStyle={{marginBottom: 20}}
        />
        <custom.InputField
          clickable={true}
          type='password'
          label='confirm password'
          placeholder='••••••••'
          icon={<svg.EyeOffSvg />}
          containerStyle={{marginBottom: 20}}
        />
        <components.Button
          title='Sign up'
          onClick={() => {
            navigate('/SignUpAccountCreated');
          }}
          style={{marginBottom: 20}}
        />
        <div style={{...utils.rowCenter(), marginBottom: 50}}>
          <text.T16 style={{marginRight: 4}}>Already have an account?</text.T16>
          <text.T16
            onClick={() => navigate('/')}
            style={{color: theme.colors.mainColor}}
          >
            Sign in.
          </text.T16>
        </div>
        {/* Sign in with social */}
        <div>
          <text.T16 style={{marginBottom: 30}}>
            Sign in with social networks:
          </text.T16>
          <div style={{...utils.rowCenterSpcBtween()}}>
            <button style={{...socialStyle}}>
              <svg.FacebooSvg />
            </button>
            <button style={{...socialStyle}}>
              <svg.TwitterSvg />
            </button>
            <button style={{...socialStyle}}>
              <svg.GoogleSvg />
            </button>
          </div>
        </div>
      </main>
    );
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
    </>
  );
};
