import axios from 'axios';
import React, {useEffect, useState} from 'react';
import {useNavigate, useLocation} from 'react-router-dom';

import {text} from '../../text';
import {hooks} from '../../hooks';
import {items} from '../../items';
import {URLS} from '../../config';
import {utils} from '../../utils';
import {svg} from '../../assets/svg';
import {theme} from '../../constants';
import {RootState} from '../../store';
import {components} from '../../components';

export const Profile: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();

  const [user, setUser] = useState<any>(null);
  const [loading, setLoading] = useState<boolean>(true);

  const {phoneVerified, emailVerified} = hooks.useAppSelector(
    (state: RootState) => state.verificationSlice
  );

  useEffect(() => {
    getData();
    window.scrollTo(0, 0);
  }, []);

  const getData = async () => {
    setLoading(true);

    try {
      const user = await axios.get(URLS.GET_USER).then((res) => res.data.user);

      setUser(user);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const renderHeader = (): JSX.Element => {
    return (
      <components.Header
        title='My profile'
        burger={true}
        basket={true}
        line={true}
      />
    );
  };

  const renderUserDetails = (): JSX.Element => {
    return (
      <div style={{marginBottom: 30, padding: '0 20px', ...utils.rowCenter()}}>
        <img
          src={user.photo}
          style={{height: 60, width: 60, marginRight: 14, borderRadius: '50%'}}
        />
        <div>
          <text.H5
            style={{
              marginBottom: 6,
              textTransform: 'capitalize',
              color: theme.colors.mainColor,
            }}
          >
            {user?.name}
          </text.H5>
          <text.T14>{user?.email}</text.T14>
        </div>
      </div>
    );
  };

  const renderMenu = (): JSX.Element => {
    return (
      <div style={{paddingLeft: 20}}>
        <items.ProfileItem
          title='Personal info'
          icon={<svg.UserSvg />}
          onClick={() => {
            navigate('/EditProfile');
          }}
          containerStyle={{marginBottom: 10}}
        />
        <items.ProfileItem
          title='My orders'
          icon={<svg.ServerSvg />}
          onClick={() => {
            navigate('/OrderHistory');
            // navigate('/OrderHistoryEmpty');
          }}
          containerStyle={{marginBottom: 10}}
        />
        <items.ProfileItem
          title='My promocodes'
          icon={<svg.GiftSvg />}
          onClick={() => {
            // navigate('/MyPromocodes');
            navigate('/MyPromocodesEmpty');
          }}
          containerStyle={{marginBottom: 10}}
        />
        <items.ProfileItem
          title={
            phoneVerified ? 'Phone number is verified' : 'Verify phone number'
          }
          onClick={() => {
            if (phoneVerified) {
              return alert('Your phone number is verified');
            }

            navigate('/SendPhoneOtp');
          }}
          goNavigation={true}
          titleStyle={{
            color: phoneVerified
              ? theme.colors.mainColor
              : theme.colors.coralRed,
          }}
          icon={
            <svg.SmartPhoneSvg
              strokeColor={theme.colors.textColor}
              fillColor={phoneVerified ? '#CFF5CE' : '#FFDCDC'}
            />
          }
          navIcon={!phoneVerified}
          containerStyle={{marginBottom: 10}}
        />
        <items.ProfileItem
          title={emailVerified ? 'Email is verified' : 'Verify email'}
          onClick={() => {
            if (emailVerified) {
              return alert('Your email is verified');
            }

            navigate('/SendEmailOtp');
          }}
          goNavigation={true}
          titleStyle={{
            color: emailVerified
              ? theme.colors.mainColor
              : theme.colors.coralRed,
          }}
          icon={
            <svg.MailSvg
              strokeColor={theme.colors.textColor}
              fillColor={emailVerified ? '#CFF5CE' : '#FFDCDC'}
            />
          }
          navIcon={!emailVerified}
          containerStyle={{marginBottom: 10}}
        />
        <items.ProfileItem
          title='Sign out'
          icon={<svg.LogOutSvg />}
          onClick={() => {
            navigate('/SignOut');
          }}
          navIcon={false}
          containerStyle={{marginBottom: 10}}
        />
        <items.ProfileItem
          title='Delete account'
          icon={<svg.DeleteSvg />}
          onClick={() => {
            navigate('/DeleteAccount');
          }}
          navIcon={false}
          titleStyle={{
            color: theme.colors.coralRed,
          }}
        />
      </div>
    );
  };

  const renderContent = () => {
    if (loading) return <components.TabLoader />;

    return (
      <main style={{paddingTop: 40, paddingBottom: 64 + 30, marginTop: 52}}>
        {renderUserDetails()}
        {renderMenu()}
      </main>
    );
  };

  const renderBottomTabBar = () => {
    return <components.BottomTabBar />;
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
      {renderBottomTabBar()}
    </>
  );
};
