import axios from 'axios';
import {useEffect, useState, FC} from 'react';
import {useNavigate} from 'react-router-dom';
import * as Accordion from '@radix-ui/react-accordion';

import {text} from '../text';
import {URLS} from '../config';
import {utils} from '../utils';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {components} from '../components';

export const OrderHistory: FC = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [ordersData, setOrdersData] = useState<any[]>([]);

  const getOrders = async (): Promise<void> => {
    setLoading(true);
    try {
      const response = await axios.get<{orders: any[]}>(URLS.GET_ORDERS);
      setOrdersData(response.data.orders);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getOrders();
  }, []);

  const renderHeader = () => {
    return <components.Header goBack={true} title='Order history' />;
  };

  const renderContent = (): JSX.Element => {
    if (loading) return <components.Loader />;

    return (
      <main style={{padding: '10px 0 20px 0', marginTop: 52}}>
        <Accordion.Root type='single' collapsible={true}>
          {ordersData.map((order, index) => {
            return (
              <Accordion.Item key={index} value={order.id}>
                {/* Trigger */}
                <Accordion.Trigger
                  style={{
                    flexDirection: 'column',
                    width: '100%',
                    display: 'flex',
                    padding: '20px 20px 14px 20px',
                    borderTop: '1px solid #E7EBEB',
                  }}
                >
                  <div
                    style={{
                      width: '100%',
                      marginBottom: 10,
                      ...utils.rowCenterSpcBtween(),
                    }}
                  >
                    <text.H5 style={{color: theme.colors.mainColor}}>
                      #{order.id}
                    </text.H5>
                    <text.H5 style={{color: theme.colors.mainColor}}>
                      ${order.total}
                    </text.H5>
                  </div>
                  <div style={{width: '100%', ...utils.rowCenterSpcBtween()}}>
                    <text.T12
                      style={{
                        textTransform: 'capitalize',
                        color:
                          order.status === 'delivered'
                            ? '#51BA74'
                            : order.status === 'shipping'
                            ? '#F5C102'
                            : '#FF4343',
                      }}
                    >
                      {order.status}
                    </text.T12>
                    <text.T12 style={{color: theme.colors.textColor}}>
                      {order.date}
                    </text.T12>
                  </div>
                </Accordion.Trigger>
                {/* Content */}
                <Accordion.Content style={{padding: '10px 20px 20px 20px'}}>
                  <div
                    style={{
                      backgroundColor: '#FAFAFA',
                      padding: 20,
                      borderRadius: 15,
                      marginBottom: 20,
                    }}
                  >
                    {order.products.map((item: any, index: any, array: any) => {
                      const isLast = index === array.length - 1;
                      return (
                        <div
                          style={{
                            width: '100%',
                            ...utils.rowCenterSpcBtween(),
                            marginBottom: isLast ? 10 : 6,
                          }}
                        >
                          <text.T14 style={{color: theme.colors.textColor}}>
                            {item.name}
                          </text.T14>
                          <text.T14 style={{color: theme.colors.textColor}}>
                            {item.quantity} x {item.price}
                          </text.T14>
                        </div>
                      );
                    })}
                    <div
                      style={{
                        height: 1,
                        width: '100%',
                        backgroundColor: '#E7EBEB',
                        marginBottom: 10,
                      }}
                    />
                    <div style={{...utils.rowCenterSpcBtween()}}>
                      <text.T14 style={{color: theme.colors.mainColor}}>
                        Total
                      </text.T14>
                      <text.T14 style={{color: theme.colors.mainColor}}>
                        ${order.total}
                      </text.T14>
                    </div>
                  </div>
                  {/* Repeat order & Leave a review */}
                  <div style={{width: '100%', ...utils.rowCenterSpcBtween()}}>
                    <button style={{lineHeight: 0}}>
                      <svg.RepeatOrderSvg />
                    </button>
                    <button
                      style={{lineHeight: 0}}
                      onClick={() => {
                        navigate('/LeaveAReview');
                      }}
                    >
                      <svg.LeaveAReviewSvg />
                    </button>
                  </div>
                </Accordion.Content>
              </Accordion.Item>
            );
          })}
        </Accordion.Root>
      </main>
    );
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
    </>
  );
};
