import axios from 'axios';
import {useState} from 'react';
import {useEffect, FC} from 'react';
import {Swiper, SwiperSlide} from 'swiper/react';
import useEmblaCarousel from 'embla-carousel-react';

import {URLS} from '../../config';
import {hooks} from '../../hooks';
import {utils} from '../../utils';
import {items} from '../../items';
import {custom} from '../../custom';
import {theme} from '../../constants';
import {actions} from '../../store/actions';
import {components} from '../../components';

import {ProductType, BannerType, CarouselType, CategoryType} from '../../types';

export const Home: FC = () => {
  const dispatch = hooks.useAppDispatch();
  const navigate = hooks.useAppNavigate();

  const [emblaRef, emblaApi] = useEmblaCarousel();
  const [activeIndex, setActiveIndex] = useState(0);
  const [loading, setLoading] = useState<boolean>(true);

  const [bannersData, setBannersData] = useState<BannerType[]>([]);
  const [productsData, setProductsData] = useState<ProductType[]>([]);
  const [carouselData, setCarouselData] = useState<CarouselType[]>([]);
  const [categoriesData, setCategoriesData] = useState<CategoryType[]>([]);

  const handleSlideChange = () => {
    if (emblaApi) {
      setActiveIndex(emblaApi.selectedScrollSnap());
    }
  };

  useEffect(() => {
    if (emblaApi) {
      emblaApi.on('select', handleSlideChange);
    }
  }, [emblaApi]);

  const getData = async () => {
    setLoading(true);

    try {
      const products = await axios
        .get(URLS.GET_PRODUCTS)
        .then((res) => res.data.products);

      const categories = await axios
        .get(URLS.GET_CATEGORIES)
        .then((res) => res.data.categories);

      const banners = await axios
        .get(URLS.GET_BANNERS)
        .then((res) => res.data.banners);

      const carousel = await axios
        .get(URLS.GET_CAROUSEL)
        .then((res) => res.data.carousel);

      setBannersData(banners);
      setProductsData(products);
      setCarouselData(carousel);
      setCategoriesData(categories);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getData();
    window.scrollTo(0, 0);
  }, []);

  const renderIndicator = (): JSX.Element => {
    return (
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          position: 'absolute',
          bottom: 20,
          left: 20,
          flexDirection: 'row',
          gap: 6,
        }}
      >
        {carouselData?.map((item: CarouselType, index: number) => {
          const isSelected = index === activeIndex;
          return (
            <div
              style={{
                background: isSelected
                  ? theme.colors.mainColor
                  : theme.colors.white,
                width: 10,
                height: 10,
                borderRadius: 5,
              }}
              key={index}
            />
          );
        })}
      </div>
    );
  };

  const renderCarouselContent = (): JSX.Element => {
    return (
      <div className='embla' ref={emblaRef}>
        <div className='embla__container'>
          {carouselData.map(
            (item: CarouselType, index: number, array: CarouselType[]) => {
              return (
                <div
                  key={item.id}
                  className='embla__slide'
                  style={{
                    position: 'relative',
                    height: 500,
                    width: '100%',
                    overflow: 'hidden',
                    cursor: 'pointer',
                  }}
                >
                  <img
                    src={item.image}
                    style={{height: '100%', objectFit: 'cover'}}
                    onClick={() => {
                      navigate('/shop', {
                        state: {products: productsData, title: 'Shop'},
                      });
                    }}
                  />
                </div>
              );
            }
          )}
        </div>
      </div>
    );
  };

  const renderCarousel = () => {
    return (
      <section style={{position: 'relative', marginBottom: 20}}>
        {renderCarouselContent()}
        {renderIndicator()}
      </section>
    );
  };

  const renderHeader = (): JSX.Element => {
    return <components.Header burger={true} basket={true} line={true} />;
  };

  const renderCategories = (): JSX.Element => {
    return (
      <div style={{marginBottom: 50}}>
        <Swiper
          spaceBetween={14}
          slidesPerView={'auto'}
          pagination={{clickable: true}}
          // onSlideChange={() => console.log('slide change')}
          // onSwiper={(swiper: any) => console.log(swiper)}
        >
          {categoriesData.map(
            (item: CategoryType, index: number, array: CategoryType[]) => {
              return (
                <SwiperSlide
                  key={item.id}
                  style={{
                    height: 90,
                    width: 90,
                    borderRadius: 10,
                    cursor: 'pointer',
                    position: 'relative',
                    display: 'flex',
                    flexDirection: 'column',
                    padding: '10px 10px 8px 10px',
                    justifyContent: 'space-between',
                    backgroundColor: theme.colors.imageBackground,
                  }}
                  onClick={() => {
                    navigate('/shop', {
                      state: {products: productsData, title: 'Shop'},
                    });
                  }}
                >
                  <img
                    src={item.image}
                    style={{
                      objectFit: 'cover',
                      width: '100%',
                      height: '100%',
                      position: 'absolute',
                      inset: 0,
                      borderRadius: 10,
                      zIndex: -1,
                    }}
                  />
                  <span
                    style={{
                      ...theme.fonts.DM_Sans_400Regular,
                      fontSize: 8,
                      backgroundColor: theme.colors.pastelMint,
                      alignSelf: 'flex-start',
                      padding: '2px 3px 1px 3px',
                      borderRadius: 50,
                      lineHeight: 1.5,
                    }}
                  >
                    {item.qty}
                  </span>
                  <span
                    style={{
                      ...theme.fonts.DM_Sans_400Regular,
                      fontSize: 10,
                      color: theme.colors.mainColor,
                      lineHeight: 1.5,
                      display: '-webkit-box',
                      WebkitBoxOrient: 'vertical',
                      WebkitLineClamp: 1,
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                      whiteSpace: 'nowrap',
                    }}
                  >
                    {item.name}
                  </span>
                </SwiperSlide>
              );
            }
          )}
        </Swiper>
      </div>
    );
  };

  const renderBestSellers = (): JSX.Element => {
    return (
      <div style={{marginBottom: 40, display: 'flex', flexDirection: 'column'}}>
        <components.BlockHeading
          title='Best sellers'
          containerStyle={{padding: '0 20px 0', marginBottom: 14}}
          viewAllOnClick={() => {
            dispatch(actions.resetFilters());
            navigate('/Shop', {
              state: {products: productsData, title: 'Best sellers'},
            });
          }}
        />
        <custom.ScrollView style={{paddingLeft: 20, paddingRight: 20}}>
          {productsData?.map(
            (item: ProductType, index: number, arra: ProductType[]) => {
              const isLast = index === arra.length - 1;
              return (
                <items.ProductCard
                  key={item.id}
                  isLast={isLast}
                  item={item}
                  version={1}
                />
              );
            }
          )}
        </custom.ScrollView>
      </div>
    );
  };

  const renderBanner = (): JSX.Element => {
    return (
      <div style={{margin: '0 20px 40px 0'}}>
        <button
          onClick={() => {
            dispatch(actions.resetFilters());
            navigate('/Shop', {state: {products: productsData}});
          }}
        >
          <img
            src={bannersData[0]?.image}
            alt='Banner'
            style={{
              width: '100%',
              borderTopRightRadius: 5,
              borderBottomRightRadius: 5,
            }}
          />
        </button>
      </div>
    );
  };

  const renderFeatured = (): JSX.Element => {
    return (
      <div style={{marginBottom: 40, display: 'flex', flexDirection: 'column'}}>
        <components.BlockHeading
          title='Featured products'
          containerStyle={{paddingLeft: 20, paddingRight: 20, marginBottom: 14}}
          viewAllOnClick={() => {
            dispatch(actions.resetFilters());
            navigate('/Shop', {
              state: {products: productsData, title: 'Featured'},
            });
          }}
        />
        <custom.ScrollView style={{paddingLeft: 20, paddingRight: 20}}>
          {productsData?.map(
            (item: ProductType, index: number, arra: ProductType[]) => {
              const isLast = index === arra.length - 1;
              return (
                <items.ProductCard
                  key={item.id}
                  isLast={isLast}
                  item={item}
                  version={3}
                />
              );
            }
          )}
        </custom.ScrollView>
      </div>
    );
  };

  const renderContent = () => {
    if (loading) return <components.TabLoader />;

    return (
      <main
        style={{
          paddingBottom: 64,
          display: 'flex',
          flexDirection: 'column',
          marginTop: 52,
        }}
      >
        {renderCarousel()}
        {renderCategories()}
        {renderBestSellers()}
        {renderBanner()}
        {renderFeatured()}
      </main>
    );
  };

  const renderBottomTabBar = () => {
    return <components.BottomTabBar />;
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
      {renderBottomTabBar()}
    </>
  );
};
