import React, {useEffect, useState} from 'react';
import {useNavigate} from 'react-router-dom';
import useThemeColor from '../hooks/useThemeColor';

import {text} from '../text';
import {utils} from '../utils';
import {custom} from '../custom';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {components} from '../components';

export const SignIn: React.FC = () => {
  const navigate = useNavigate();

  const [themeColor, setThemeColor] = useState('#fff');

  useEffect(() => {
    setThemeColor('white');
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useThemeColor(themeColor);

  const [rememberMe, setRememberMe] = useState<boolean>(false);

  const renderHeader = () => <components.Header title='SignIn' />;

  const renderContent = (): JSX.Element => {
    const socialStyle: React.CSSProperties = {
      width: '30%',
      height: 40,
      ...utils.flexCenter(),
      borderRadius: '10px',
      border: '1px solid #E8EFF4',
    };

    return (
      <main style={{padding: '50px 20px 20px 20px', marginTop: 52}}>
        <text.H1 style={{marginBottom: 14}}>Welcome Back!</text.H1>
        <text.T16 style={{marginBottom: 40}}>Sign in to continue</text.T16>
        <div>
          <custom.InputField
            label='email'
            icon={<svg.InputCheckSvg />}
            containerStyle={{marginBottom: 20}}
            placeholder='zenithsneaks@mail.com'
          />
          <custom.InputField
            label='password'
            placeholder='••••••••'
            icon={<svg.EyeOffSvg />}
            containerStyle={{marginBottom: 20}}
          />
        </div>
        {/* Remember me and forgot */}
        <div style={{...utils.rowCenterSpcBtween(), marginBottom: 30}}>
          {/* Remember me */}
          <div
            style={{cursor: 'pointer', ...utils.rowCenterSpcBtween()}}
            onClick={() => setRememberMe(!rememberMe)}
          >
            <div
              style={{
                width: 18,
                height: 18,
                borderRadius: 5,
                border: '2px solid #E8EFF4',
                ...utils.flexCenter(),
              }}
            >
              {rememberMe && <svg.InputCheckSvg />}
            </div>
            <text.T14 style={{marginLeft: 12}}>Remember me</text.T14>
          </div>
          {/* Forgot password */}
          <text.T16
            style={{cursor: 'pointer'}}
            onClick={() => navigate('/ForgotPassword')}
          >
            Forgot password?
          </text.T16>
        </div>
        {/* Button */}
        <components.Button
          title='Sign in'
          onClick={() => {
            navigate('/TabNavigator');
          }}
          style={{marginBottom: 20}}
        />
        {/* Don't have an account */}
        <div style={{...utils.rowCenter(), marginBottom: 50}}>
          <text.T16 style={{marginRight: 4}}>Don’t have an account?</text.T16>
          <text.T16
            onClick={() => navigate('/SignUp')}
            style={{cursor: 'pointer', color: theme.colors.mainColor}}
          >
            Sign up.
          </text.T16>
        </div>
        {/* Sign in with social */}
        <div>
          <text.T16 style={{marginBottom: 30}}>
            Sign in with social networks:
          </text.T16>
          <div style={{...utils.rowCenterSpcBtween()}}>
            <button style={{...socialStyle}}>
              <svg.FacebooSvg />
            </button>
            <button style={{...socialStyle}}>
              <svg.TwitterSvg />
            </button>
            <button style={{...socialStyle}}>
              <svg.GoogleSvg />
            </button>
          </div>
        </div>
      </main>
    );
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
    </>
  );
};
