import {useState, useEffect} from 'react';
import {useNavigate} from 'react-router-dom';

import {text} from '../text';
import {hooks} from '../hooks';
import {utils} from '../utils';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {RootState} from '../store';
import {actions} from '../store/actions';

type Props = {
  title?: string;
  line?: boolean;
  burger?: boolean;
  goBack?: boolean;
  basket?: boolean;
};

export const Header: React.FC<Props> = ({
  line,
  title,
  burger,
  goBack,
  basket,
}) => {
  const navigate = useNavigate();
  const [modal, setModal] = useState(false);

  const dispatch = hooks.useAppDispatch();

  useEffect(() => {
    if (modal) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }

    return () => {
      document.body.style.overflow = '';
    };
    // document.body.style.overflow = 'hidden';
  }, [modal]);

  const cart = hooks.useAppSelector((state: RootState) => state.cartSlice.list);
  const total = hooks.useAppSelector(
    (state: RootState) => state.cartSlice.total
  );

  const handleOnClick = () => {
    if (cart.length > 0) {
      dispatch(actions.setScreen('Order'));
      navigate('/TabNavigator');
      return;
    }

    return alert('Your cart is empty');
  };

  const renderGoBack = (): JSX.Element | null => {
    if (goBack) {
      return (
        <button
          style={{position: 'absolute', left: 0, padding: 20}}
          onClick={() => navigate(-1)}
        >
          <svg.GoBackSvg />
        </button>
      );
    }

    return null;
  };

  const renderBurger = (): JSX.Element | null => {
    if (burger) {
      return (
        <button
          style={{
            position: 'absolute',
            left: 0,
            padding: 20,
            cursor: 'pointer',
            backgroundColor: 'transparent',
            border: 'none',
          }}
          onClick={() => setModal(true)}
        >
          <svg.BurgerSvg />
        </button>
      );
    }

    return null;
  };

  const renderTitle = (): JSX.Element => {
    return (
      <text.T16
        style={{
          left: '50%',
          textAlign: 'center',
          position: 'absolute',
          width: 'calc(100% - 100px)',
          transform: 'translateX(-50%)',
          color: theme.colors.mainColor,
        }}
      >
        {title}
      </text.T16>
    );
  };

  const renderBasket = (): JSX.Element | null => {
    if (basket) {
      return (
        <button
          onClick={handleOnClick}
          style={{
            position: 'absolute',
            right: 0,
            paddingTop: 12,
            paddingBottom: 12,
            paddingLeft: 20,
            paddingRight: 20,
            borderRadius: 12,
          }}
        >
          <svg.BasketSvg />
          <span
            style={{
              padding: '3px 6px 3px 6px',
              borderRadius: 30,
              right: 40,
              bottom: 16,
              position: 'absolute',
              fontSize: 10,
              color: theme.colors.white,
              ...theme.fonts.DM_Sans_700Bold,
              backgroundColor: theme.colors.mainColor,
            }}
          >
            {cart.length > 0 ? `$${total.toFixed(2)}` : '$0'}
          </span>
        </button>
      );
    }

    return null;
  };

  const headerStyle: React.CSSProperties = {
    top: 0,
    height: 52,
    width: '100%',
    position: 'fixed',
    zIndex: 4,
    ...utils.rowCenterSpcBtween(),
    backgroundColor: theme.colors.white,
    borderBottom: line ? `1px solid ${theme.colors.aliceBlue2}` : 'none',
  };

  return (
    <>
      <header style={{...headerStyle}} className='container'>
        {renderGoBack()}
        {renderBurger()}
        {renderTitle()}
        {renderBasket()}
      </header>
    </>
  );
};
